import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';

import { Thin2Logger } from 'thin2-log';

@Component({
    selector: 'another-lazy-component',
    templateUrl: './another-lazy.component.html'
})
export class AnotherLazyComponent implements OnInit {
    public pathParamValue: string;

    constructor(private route: ActivatedRoute, private LOGGER: Thin2Logger) {}

    public ngOnInit() {
        this.pathParamValue = this.route.snapshot.params['second'];
        this.LOGGER.debug(`Read ${this.pathParamValue} from path parameter 'second'`);
    }
}
